import React, { Component } from 'react';
import { connect } from "react-redux";
import { Redirect, Route, Switch } from 'react-router-dom';
import UserManage from '../containers/System/User/UserManage';
import ProductManage from '../containers/System/ProductManage';
import RegisterPackageGroupOrAcc from '../containers/System/RegisterPackageGroupOrAcc';
import ManageProduct from '../containers/System/ManageProduct';
import BookManager from '../containers/System/Product/BookManager';
import NhacungcapManager from '../containers/System/Product/NhacungcapManager';
import nhaxuatbanManager from '../containers/System/Product/nhaxuatbanManager';
import TheloaiManager from '../containers/System/Product/TheloaiManager';
import Discount from '../containers/System/Product/Discount';
import BookManageInfo from '../containers/System/Product/BookManageInfo';
import BillManage from '../containers/System/Bill/BillManage';
import BillDetailManage from '../containers/System/Bill/BillDetailManage';

class System extends Component {
    render() {
        const { systemMenuPath } = this.props;
        return (
            <div className="system-container">
                <div className="system-list">
                    <Switch>
                        <Route path="/system/user-manage" component={UserManage} />
                        <Route path="/system/book-manage" component={BookManager} />
                        <Route path="/system/nhacungcap-manage" component={NhacungcapManager} />
                        <Route path="/system/nhaxuatban-manage" component={nhaxuatbanManager} />
                        <Route path="/system/theloai-manage" component={TheloaiManager} />
                        <Route path="/system/discount-manage" component={Discount} />
                        <Route path="/system/bill-manage" component={BillManage} />
                        <Route path="/system/bill-detail-manage" component={BillDetailManage} />


                        {/* /system/detail-book-manage */}
                        <Route path="/system/detail-book-manage" component={BookManageInfo} />

                        <Route path="/system/register-package-group-or-account" component={RegisterPackageGroupOrAcc} />
                        <Route component={() => { return (<Redirect to={systemMenuPath} />) }} />
                    </Switch>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        systemMenuPath: state.app.systemMenuPath
    };
};

const mapDispatchToProps = dispatch => {
    return {
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(System);
