import React, { Component } from 'react';
import { FormattedMessage } from 'react-intl';
import { connect } from 'react-redux';
import './Listchooseaction.scss'
import { withRouter } from 'react-router';


class ListChooseaction extends Component {

    constructor(props) {
        super(props)
        this.state = {

        }
    }

    componentDidUpdate(prevProps, prevState, snapshot) {

    }

    componentDidMount() {
    }

    handleClickInfoUser = () => {
        let { userInfo } = this.props;
        if (this.props.history) {
            this.props.history.push(
                {
                    pathname: `/user/${userInfo && userInfo.data ? userInfo.data.id : 'id'}`,
                    state: this.props.userInfo
                }
            )
        }
    }

    handleClickMybill = () => {
        if (this.props.history) {
            this.props.history.push('/list-bill')
        }
    }

    handleClickwarning = () => {
        alert('Chc nng cha h tr')
    }


    render() {
        return (
            <div className='list-choose-container'>
                <div className='list-choose-content'>
                    <div className='user-info-name'>
                        <div className='user-info-logo'></div>
                        <div className='user-info-name-user'>
                            <div className='user-name-title'>Ti khon ca</div>
                            <div className='user-is-name'>Cng nguyn</div>
                        </div>
                    </div>
                    <div className='user-info-choose'
                        onClick={() => this.handleClickInfoUser()}
                    >
                        <i className="fas fa-user"></i>
                        <span className='text-info-choose'>Thng tin ti khon</span>
                    </div>
                    <div className='user-info-choose'
                        onClick={() => this.handleClickwarning()}

                    >
                        <i className="fas fa-bell"></i>
                        <span className='text-info-choose'>Thng bo ca ti</span>
                    </div>
                    <div className='user-info-choose'
                        onClick={() => this.handleClickMybill()}

                    >
                        <i className="fas fa-book-open"></i>
                        <span className='text-info-choose'>Qun l n hng</span>
                    </div>
                    <div className='user-info-choose'
                        onClick={() => this.handleClickwarning()}

                    >
                        <i className="fas fa-box"></i>
                        <span className='text-info-choose'>Qun l i tr</span>
                    </div>
                    <div className='user-info-choose'
                        onClick={() => this.handleClickwarning()}

                    >
                        <i className="fas fa-map-marker-alt"></i>
                        <span className='text-info-choose'>S a ch</span>
                    </div>
                    <div className='user-info-choose'
                        onClick={() => this.handleClickwarning()}

                    >
                        <i className="far fa-credit-card"></i>
                        <span className='text-info-choose'>Thng tin thanh ton</span>
                    </div>
                </div>
            </div>
        )
    }

}

const mapStateToProps = state => {
    return {
        userInfo: state.user.userInfo,

    };
};

const mapDispatchToProps = dispatch => {
    return {
    };
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(ListChooseaction));
